.json2agilereport table {
    border-top: 1px solid #ddd;
    border-spacing: unset;
    border-left: 1px solid #ddd;
}
.json2agilereport td,.json2agilereport th {
    border-bottom: 1px solid #ddd;
    border-right:1px solid #ddd;
    padding: 2px 10px; 
    font-size: 13px;
    white-space: nowrap;
}
.json2agilereport tbody tr:nth-child(2n) {background-color: #dbe4ec;}
.json2agilereport tbody tr:hover {
    background: #98bfcc;
    color: #fff;
}
.json2agilereport {
    color: #756c6c;
    display: inline-block;
    max-height: 350px;
    overflow: auto;
}
.agile_wrapper{
  font-family: sans-serif;
}
.theader {
    padding: 6px 2px;
    background-color: #676786;
    color: #d8d0d0;
    text-align: center;
    margin-bottom: 5px;
}
.loading {
    background: url('../image/ajax-loader.gif') no-repeat center center;
    height: 100%;
}
.agile_top_tools input {
    width: 33%;
}
.agile_down {
    width: 30px;
    height: 25px;
    cursor: pointer;
    float: right;
    background: url(../image/down_img.png) no-repeat center center / 18px;
    position: relative;
}
ul.agile_suggest {
    position: absolute;
    margin: 0;
    padding: 0;
    width: 200px;
    display: none;
    background: #dbe4ec;
    box-shadow: -1px 1px 9px 1px grey;
}
ul.agile_suggest li {
    border-bottom: 1px solid gray;
    list-style-type: none;
    font-size: 13px;
    padding: 2px 10px;
    color: #756c6c;
    cursor: pointer;
}
ul.agile_suggest li:hover {
    background-color: #98bfcc;
    color: #FFF;
}
.pagination {float: right;box-shadow: -1px 2px 6px 0px grey;}
.agile_bottom_tools {height: 27px;padding-top: 4px;}
.pagination a.active {background-color: #4CAF50;color: white;}
.pagination a:hover:not(.active) {background-color: #ddd;}
.agile_bottom_tools input {width: 30px;border: none;text-align: right;}
.agile_bottom_tools span {font-size: 11px;font-style: italic;}
.agile_bottom_tools input:focus {outline: none;border: 1px solid #ccc;}
.pagination a {color: black;float: left;padding: 2px 6px;text-decoration: none;font-size: 12px;}
a.agile_hide {display: none;}
tr.agile_sort th {cursor: pointer;}
tr.agile_sort th.asc:after {
    content: "\f0de";
    font: normal normal normal 14px/1 FontAwesome !important;
    float: right;
}
tr.agile_sort th.desc:after {
    content: "\f0dd";
    font: normal normal normal 14px/1 FontAwesome !important;
    float: right;
}
.agile_down ul {
    list-style-type: none;
    padding: 5px;
    position: absolute;
    margin: 25px 0 0 0;
    box-shadow: 0px 0px 5px 1px #a89e9e;
    background-color: #dbe4ec;
    font-size: 13px;
    display: none;
    right: 0;
}
.agile_down ul li {
    border-bottom: 1px solid #ccc;
    padding: 5px;
    white-space: nowrap;
}
.agile_down ul li:hover {
    background-color: #98bfcc;
    color: white;
}
.agile_down:hover ul {
    display: block;
}
.agile_down ul li:last-child {
    border: none;
}